
.main-products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #EAEDED;
    gap: 1rem;
    padding: 1.25rem 1.25rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    font-family: "Poppins";
}

.single-card{
    width: 15rem;
    padding: 1rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    border-radius: 5px;
    gap: 1rem;
    cursor: pointer;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.single-card:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


.single-card > div {
    width: 100%;
    text-align: center;
    justify-content: center;
}

.product-image{
    width: 12rem;
    padding: 1rem 1rem;
}

.product-title{
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.2px;
    color: #37475A;
    
}

.product-title:hover{
    color: #70A401;
    text-decoration: none;
}


.product-price{
    color: #70A401;
    font-weight: bold;
}

.btn-view-product{
    border: none;
    height: 2rem;
    border-radius: 5px;
    width: 6rem;
    background-color: #70A401;
}




.product-container{
    background-color: #EDF1F2;
    display: flex;
    justify-content: center;
    padding: 1rem 1rem;
}

.single-product-container{
    display: flex;
    border-radius: 5px;
    width: 100%;
    padding: 1rem 1rem;
    justify-content: space-evenly;
    flex-wrap: wrap;
    border: 1px solid lightgray;
    background-color: white;
    font-family: "Poppins";
}

.single-product-details-container{
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 1rem 1rem;
    text-align: left;
    gap: 0.5rem;
    width: 400px;
}

.single-product-image{
    width: 25rem;
}

.single-product-screen-title{
    color: #37475A;
}

.single-product-screen-title:hover{
    text-decoration: none;
    color: #70A401;
}

.single-product-description{
    font-size: 14px;
    letter-spacing: 0.5px;
    text-align: left;
}

.single-product-screen-price{
    font-size: 2rem;
    color: #70A401;
}

.single-product-view-quantity{
    width: 4rem;
    height: 2rem;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
    font-size: 15px;
    padding-left: 0.5rem;
}

.single-product-view-quantity:focus{
    outline: none;
}

.add-cart-container{
    padding: 1rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: start;
    text-align: start;
}

.add-cart-btn{
    padding: 1rem 1rem;
    text-transform: uppercase;
    width: 15rem;
    border: none;
    font-weight: bold;
    letter-spacing: 1px;
    border-radius: 5px;
    font-size: 18px;
    justify-content: center;
    display: flex;
    gap: 5px;
    align-items: center;
    color: white;
    background-color: #70A401;
}

@media screen and (max-width: 800px) {

    .single-product-container{
        justify-content: start;
    }

    .single-product-image{
        width: 100%;
    }
    
}



