
.main-login-container{
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #EAEDED;
    align-items: center;
    padding: 1rem 1rem;
    font-family: 'Poppins',sans-serif;

}

.login-container{
    background-color: white;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 2rem 2rem;
    border-radius: 5px;
    border: 1px solid lightgray;
    width: 70%;
    gap: 1rem;
}

.login-left-container{
    width: 375px;
}

.login-right-container{
    display: flex;
    justify-content: start;
    background-color: white;
    flex-direction: column;
    padding: 1.5rem 1.5rem;
    border-radius: 5px;
    width: 375px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
}

.lbl-login{
    font-size: 14px;
    color: grey;
}

.txt-login{
    border-radius: 5px;
    padding: 10px 10px;
    padding-left: 1rem;
    border: none;
    width: 100%;
    font-size: 13px;
    color: #232F3E;
    border: 1px solid lightgray;
}

.txt-login:focus{
    outline: none;
}

.btn-login{
    border: none;
    padding: 0.5em 0.5rem;
    font-size: 16px;
    font-weight: 550;
    width: 100%;
    color: white;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    background-color: #70A401;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.btn-login:hover{
    opacity: 0.9;
}

.login-links{
    color: #232F3E;
}

.login-links:hover{
    color: #70A401;
}

.btn-google{
    height: 3rem;
    background-color: white;
    color: #232F3E;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 100%;
    gap: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
}



@media screen and (max-width: 800px) {
    .login-container{
        width: 100%;
        flex-wrap: wrap;
        justify-content: center;
        padding: 1.25rem 1.25rem;
        padding-top: 1.5rem;
    }
}